@import '../../assets/mixins.module.scss'; 
@import '../../assets/colors.module.scss'; 

.wrapper{
  width: 100%;
  max-width: 900px;
  min-height: 100vh;
  font-size: 1.5rem;
  padding: $padding-topBar-sync 20px 20px 20px;
  margin: 0 auto;
  background-color: $white-smoke;

  .cartTabs{
    height: 40px;
    width:80%;
    margin:20px auto;
    border-bottom: 1px dotted gray;

    .cartTab{
      line-height: 40px;
    }
  }

  .productsList{
    
    .product{
      display: grid;
      grid-template-columns: repeat(10, 1fr);
      grid-template-rows: repeat(10, 1fr);
      min-height:100px;
      max-width: 400px;
      margin: 10px auto;
      box-shadow: 0 0 5px $silver;
      
      .productImg{
        position: relative;
        grid-column: 1/3;
        grid-row: 1/11;
        width: 100%;
        height: 100%;
        object-fit: fill;
      }
      .productTitle{
        position: relative;
        grid-column: 3/8;
        grid-row: 1/4;
        align-self: center;
        text-overflow: ellipsis;
        padding-left: 15px;
        font-weight: 500;
       
      }
      .productPrice{
        position: relative;
        grid-column: 7/11;
        grid-row: 7/9;
        justify-self: center;
        align-self: center;
        font-weight: 700;
        
      }
      .removeProductBtn{
        position: relative;
        grid-column: 9/11;
        grid-row: 1/4;
        font-size: 2rem;
        }
    }    
  }
}

.centerVH{
  @include centerVH;
}

.centerV{
  @include centerV;
}

.centerH{
  @include centerH;
}

@media(min-width:950px){
  .wrapper{
    .cartTabs{
      margin: 10px auto 30px auto;
    }
  }
  
}